@import url(https://fonts.googleapis.com/css?family=Poppins:400,700,900&display=swap);
* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
}

body {
	font-family: 'Poppins', sans-serif;
	font-weight: normal;
	font-size: 100%;
	margin: 0;
	padding: 0;
}

#root {
	width: 600px;
	height: 600px;
	margin: 0 auto;
}
.board {
	height: 80%;
	display: grid;
}

.board-drums {
	grid-template-columns: 25% 25% 25%;
}

.board-synth {
	grid-template-columns: 25% 25% 25% 25%;
}

.key {
	display: flex;
	align-items: flex-end;
	
	margin: 0.3em 0.3em;
	padding-top: 50%;
	/* padding: 0.3em; */
	color: #dbdbdb;
	text-shadow: 0px 1px 0px #a8a8a8;
	background: #bdbbbb;
	border-bottom: 2px solid #8a8888;
	border-left: 2px solid #8a8888;
	border-radius: 3px;
	
	
}

.key-active {
	background: #8a8888;
	border-top: 2px solid #ef7508;
	border-right: 2px solid #ef7508;
	border-bottom: none;
	border-left: none;
}

